.btn {
  background-color: #43963f;
  text-decoration: none;
  border-radius: 10px;
  padding: 0.5em 0.75em;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-weight: bold;
  letter-spacing: 0.1em;
  cursor: pointer;
  position: relative;
}

.btn:hover,
.btn:focus {
  opacity: 0.65;
}

.btn--fullwidth {
  display: block;
}

.post-item {
  display: flex;
  width: 100%;
  padding: 0.5em;
}

.post-item__content {
  background-color: blue;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.post-item__img {
  display:block;
  margin: 0 auto;
  max-width: 200px;
  max-height: 300px;
  margin-bottom: 1em;
}

.post__title {
  font-size: 2rem;
  font-style: italic;
  font-weight: 900;
  color: #43963f;
  margin-top: 0;
  margin-bottom: 0.3em;
}

.icon {
  margin-right: 0.5em;
  margin-top: 0;
}

.post__metadata {
  margin-bottom: 0.5em;
  font-size: .8rem;
}

.post__intro {
  line-height: 1.4em;
}

@media (min-width:600px) {
  .post-item {
    width: 50%;
  }
}

@media (min-width:900px) {
  .post-item {
    width: 33%;
  }
}